.toDo {
    position: relative;
    display: flex;
    justify-content: center;
    min-height: 100vh;
    overflow: hidden;

    &__container {
        margin: 0 20px;
        width: 100%;
        max-width: 1200px;
        text-align: center;
    }

    &__header {
        margin-top: 20px;
        margin-bottom: 80px;

        text-transform: uppercase;
    }
}
